import QSPopup from '../../components/QS-popup/QS-popup.vue';
export default {
	components: {
		QSPopup
	},
	data(){
		return {
			//用户选择
			anchorIndex: 1,
			perIndex: 1,
			//弹出层
			showRight: false,
			customerInfoShow:'',
			//行业划分
			list: [
				'电子信息',
				'新材料',
				'生物及医药',
				'汽车制造业',
				'现代食品制造'
			],
			userList: {
				trade: '按照行业划分',
				area: '区域划分',
				age: '法人年龄'
			},
			//查询关键字
			query: '',
		}
	},
	props:{
		placeHolder: {
			type: String,
		},
	},
	methods:{
		//点击按钮变色
		btnClick(e){
			if(e.target.dataset.index != 2 ){
			    this.anchorIndex = 1;
			}else{
			    //获取当前点击事件的下标
			    this.anchorIndex =  e.target.dataset.index
			}
		},
		perClick(e){
			if(e.target.dataset.index != 2 ){
			    this.perIndex = 1;
			}else{
			    //获取当前点击事件的下标
			    this.perIndex =  e.target.dataset.index
			}
		},
		//弹出层
		show(ref) {
			this.$refs[ref].show();
		},
		//搜索
		remoteMethod () { 
		    var keyword = this.query
		    if(keyword) {
		        ocation.href = "#/index?keyword="+keyword;
		    }else {
		        alert('请输入搜索内容')
		    }
		    var keyword = getParamsByUrl (location.href,'keyword')
		    function getParamsByUrl (url,name) {
		        var params=url.substr(url.indexOf('?')+1);
		        var param=params.split();
		        for(var i=0;i<param.length;i++){
		            var current=param[i].split('=');
		            if(current[0]==name){
		                return current[1]
		            }
		        }
		            return null;
		    }
			uni.request({
				url: '',
				data: '',
				success: (res) => {
					console.log(res)
					if (res.status == 200) {
					    this.list = res.data.data
					    console.log(this.list);
					} else {
					    alert('获取列表失败')
					}
				}
			})
		}
	}
}
